html, body {
  font-size: 16px;
  background: #fffffd;
  font-family: sans-serif; }

hr {
  border-color: #105952;
  border-width: rem-calc(2px);
  border-style: solid; }

.form-field-wrapper {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%; }
  .form-field-wrapper.inline {
    display: inline-flex;
    justify-content: center;
    align-items: center; }
  .form-field-wrapper :nth-child(1).form-field {
    flex: 1;
    margin: 0 .25rem 1rem .5rem; }
  .form-field-wrapper :nth-child(2).form-field {
    margin: 0 .5rem 1rem .25rem; }
    .form-field-wrapper :nth-child(2).form-field button {
      width: 4rem; }

#rooms_list {
  padding: 1rem .5rem;
  display: block;
  text-align: center; }

ul#list {
  padding-bottom: 4rem; }
