    width: .75rem; }
  .spinner .circle.small {
    height: 1rem;
    width: 1rem; }
  .spinner .circle.medium {
    height: 1.25rem;
    width: 1.25rem; }
  .spinner .circle.large {
    height: 1.75rem;
    width: 1.75rem; }
  .spinner .circle.xlarge {
    height: 2.5rem;
